<template>
    <div>
        <van-nav-bar title="账号管理" left-text="返回" left-arrow @click-left="$router.go(-1)" />

        <van-cell-group>
            <van-cell title="头像">
                <template #right-icon>
                    <van-uploader :after-read="portrait">
                        <img class="auto-img" :src="userInfo.userImg" alt="">
                    </van-uploader>
                </template>
            </van-cell>
            <van-cell center title="用户id" :value="userInfo.userId" />
            <van-cell center title="手机号" :value="userInfo.phone" />

            <van-cell center title="昵称">
                <van-field
                    v-model="userInfo.nickName"
                    input-align="right"
                    @blur="editInfo({ key: 'nickName', val: userInfo.nickName, url: '/updateNickName' })"
                />
            </van-cell>

            <van-cell class="userDesc" title="简介">
                <textarea v-model="userInfo.desc" @blur="editInfo({ key: 'desc', val: userInfo.desc, url: '/updateDesc' })"></textarea>
            </van-cell>
        </van-cell-group>
    </div>
</template>

<script>
    import { Toast } from "vant";

    export default {
        data() {
            return {
                // 用户信息
                userInfo: {},
            };
        },

        methods: {

            // 获取用户信息
            getUserInfo() {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "GET",
                    url: "/findAccountInfo",
                    params: {
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code = "B001") {
                        this.userInfo = res.data.result[0];
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 上传头像
            portrait(file) {

                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                let fileImg = file.content.split(",");

                let imgType = ["jpg", "jpeg", "png", "gif"]

                this.axios({
                    method: "POST",
                    url: "/updateAvatar",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        ...imgType,
                        serverBase64Img: fileImg[1],
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == "H001") {

                        this.userInfo.userImg = res.data.userImg;

                    } else {
                        Toast.fail(res.data.code);
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 修改信息
            editInfo(obj) {
                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: obj.url,
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        [obj.key]: obj.val,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    if(res.data.code == "C001" || res.data.code == "D001") {

                        Toast.success(res.data.msg);

                    } else{
                        Toast.fail(res.data.msg);
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            }

        },

        created() {
            // 获取用户信息
            this.getUserInfo();
        }
    }
</script>

<style lang="less" scoped>
    .van-nav-bar{
        height: 45px;
    }

    .van-cell__title{
        line-height: 35px;
    }

    .van-uploader{
        width: 35px;
    }

    /deep/ .van-field__control{
        color: #969799;
    }

    .userDesc{
        display: block;

        .van-cell__value{
            text-align: left;
        }

        textarea {
            width: 100%;
            border: none;
            resize:none;
        }
    }

</style>